<template>
 <view class="body">
  <uni-popup ref="popup" type="message">
   <uni-popup-message type="success" :message="'成功切换为' + showColor" :duration="2000"></uni-popup-message>
  </uni-popup>
  <view class="canvans" disable-scroll="true">
   <canvas class='canvas' style="height: 80vh;width: 100%;position: absolute" canvas-id="mycanvas"/>
   <img src="../../static/background.jpeg" alt="" style="width: 100%;height: 80vh;position: absolute;left: 0;top: 0;">
   <movable-area class='img-box width-full' style="width:100%; height:80vh">
    <!-- 贴图开始 -->
    <view v-for="(item, index) in chooseImages" :key="item.key">
     <movable-view v-if="item.chosedImg" :x="item.x" :y="item.y" direction="all" :data-item="item.idx">
      <view class='sticker-box'>
       <image class='sticker width-full' mode="widthFix" :src="item.url"
        @touchstart="onTouchStart(item, $event)" @touchmove="onTouchMove(item, $event)"
        @touchend="onTouchEnd(item, $event)"></image>
      </view>
      <image class='cancel' @click='cancel(item, event)' src='../../static/cancel.png'></image>
     </movable-view>
    </view>
    <!-- 贴图结束 -->
   </movable-area>
  </view>
  <view class='bottom'>
   <view class="sticker-lists-body">
    <scroll-view class="recommend_scroll_x_box" scroll-x="true">
     <view class="sticker-list" v-for="(item, index) in stickers" :data-url="item"
      @click='changeImg(item)'>
      <image :src='item.url'></image>
     </view>
    </scroll-view>
   </view>
   <view class='tab'>
    <button type="warn" @click='createNewImg'>保存</button>
   </view>
  </view>
 </view>
</template>

<script>
 import {
  stickers,
  chooseImages
 } from './imgs.js'
 export default {
  data() {
   return {
    canvasH: 0,
    canvasW: 0,
    dom: null,
    ctx: null,
    timer: null,
    position: {
     x: Number,
     y: Number
    },
    canvansSize: {
     x: Number,
     y: Number
    },
    redoList: [],
    undoList: [],
    Colors: [{
      className: 'color_1',
      color: '#eeeeee',
      fontColor: '白色'
     },
     {
      className: 'color_2',
      color: '#000',
      fontColor: '黑色'
     },
     {
      className: 'color_3',
      color: '#ff0000',
      fontColor: '红色'
     },
     {
      className: 'color_4',
      color: '#FAE81B',
      fontColor: '黄色'
     },
     {
      className: 'color_5',
      color: '#283CF0',
      fontColor: '绿色'
     },
     {
      className: 'color_6',
      color: '#F01AA6',
      fontColor: '粉色'
     },
     {
      className: 'color_1',
      color: '#eeeeee',
      fontColor: '白色'
     },
     {
      className: 'color_2',
      color: '#000',
      fontColor: '黑色'
     },
     {
      className: 'color_3',
      color: '#ff0000',
      fontColor: '红色'
     },
     {
      className: 'color_4',
      color: '#FAE81B',
      fontColor: '黄色'
     },
     {
      className: 'color_5',
      color: '#283CF0',
      fontColor: '绿色'
     },
     {
      className: 'color_6',
      color: '#F01AA6',
      fontColor: '粉色'
     },
     {
      className: 'color_1',
      color: '#eeeeee',
      fontColor: '白色'
     },
     {
      className: 'color_2',
      color: '#000',
      fontColor: '黑色'
     },
     {
      className: 'color_3',
      color: '#ff0000',
      fontColor: '红色'
     },
     {
      className: 'color_4',
      color: '#FAE81B',
      fontColor: '黄色'
     },
     {
      className: 'color_5',
      color: '#283CF0',
      fontColor: '绿色'
     },
     {
      className: 'color_6',
      color: '#F01AA6',
      fontColor: '粉色'
     },
    ],
    chooseImages: [],
    showColor: '',
    stickers: [],
    x: 160,
    y: 50,
    chosedImg: false,
    stv: {
     offsetX: 160,
     offsetY: 50,
     zoom: false, //是否缩放状态
     distance: 0, //两指距离
     scale: 1, //缩放倍数
     width: 50,
     height: 50,
    },
    loadImagePath: ''
   }
  },
  methods: {
   savaCanvans() {
    uni.canvasToTempFilePath({
     x: 0,
     y: 0,
     canvasId: 'canvas',
     success: (res) => {
      this.redoList = [res.tempFilePath, ...this.redoList]
      // console.log(res.tempFilePath)
      this.undoList = []
     }
    })
   },
   onTouchStart(item, e) {
    // console.log(item)
    const event = e.touches[0];
    item.x = event.pageX
    item.y = event.pageY
   },
   onTouchMove(item, e) {
    const event = e.touches[0];
    item.x = event.pageX
    item.y = event.pageY
   },
   clearCanvans() {
    this.ctx.clearRect(0, 0, 1500, 1500);
    this.ctx.draw(true);
    const redoList = this.redoList.slice(1);
    this.undoList = []
   },
   // 重做
   redo() {
    // this.ctx.drawImage(this.redoList[1], 0, 0, this.canvansSize.x, this.canvansSize.y);
    // this.ctx.draw();
    // this.undoList.unshift(this.redoList.shift());
    // this.redoList = this.redoList
    // this.undoList = this.undoList

   },
   // 撤销
   undo() {
    this.ctx.drawImage(this.undoList[0], 0, 0, this.canvansSize.x, this.canvansSize.y);
    this.ctx.draw();
    this.redoList.unshift(this.undoList.shift());
    this.redoList = this.redoList,
     this.undoList = this.undoList
   },
   onTouchEnd(item, e) {
    const event = e.touches[0];
    item.x = event.pageX
    item.y = event.pageY
   },
   changeColor(item) {
    this.ctx.setStrokeStyle(item.color);
    this.showColor = item.fontColor
    this.$refs.popup.open()
   },
   changeImg(item) {
    const random = Math.random() * 10
    const obj = {
     idx: true,
     x: 160,
     y: 50,
     stv: {
      offsetX: 160,
      offsetY: 50,
      zoom: false, //是否缩放状态
      distance: 0, //两指距离
      scale: 1, //缩放倍数
      width: 50,
      height: 50,
     },
     chosedImg: true,
     url: item.url,
     key: item.url + random
    }
    this.chooseImages.push(obj)
    // this.ctx.setStrokeStyle("");
   },
   cancel(item) {
    item.chosedImg = false,
    item.x = 150
    item.y = 75
    item.stv = {
     offsetX: 75,
     offsetY: 75,
     zoom: false, //是否缩放状态
     distance: 0, //两指距离
     scale: 1, //缩放倍数
     width: 50,
     height: 50,
    }
   },
   onTouchStart2() {
    console.log('test')
   },
   onTouchMove2() {
    console.log('test')
   },
   onTouchEnd2() {
    console.log('test')
   },
   createNewImg() {
    const that = this
    const width= wx.getSystemInfoSync().screenWidth;
    const height=wx.getSystemInfoSync().screenHeight*0.8;
    let ctx = wx.createCanvasContext('mycanvas', this)
    //绘制图片
    ctx.drawImage('../../static/background.jpeg', 0, 0, width, height) // 绘制背景图
    for(let i=0;i<this.chooseImages.length;i++){
     const item=this.chooseImages[i]
     ctx.drawImage(item.url, item.x, item.y, 50, 50) 
     // console.log(item)
    }
    // 显示绘制
    ctx.draw()
    
    //将生成好的图片保存到本地，需要延迟一会，绘制期间耗时
    this.timer = setTimeout(() => {
     wx.canvasToTempFilePath({
      canvasId: 'mycanvas',
      x: 0, // 画布区域左上角坐标
      y: 0, // 画布区域左上角坐标
      width: width,
      height: height,
      // destWidth: imgW*2,  // 决定了保存图片的清晰程度
      // destHeight: imgH*2,  // 决定了保存图片的清晰程度
      success: function(res) {
       let tempFilePath = res.tempFilePath
       that.loadImagePath = tempFilePath // 本地图片地址，现在可以下载了
       uni.showLoading({
        title: '加载中'
       });
       uni.uploadFile({
        filePath: tempFilePath,
        name: 'file',
        url: 'https://www.rykerfeng.cn/travel2/backend/test/uploadImg.php',
        success(res) {
         if(JSON.parse(res.data).code == "0") {
          uni.hideLoading();
          uni.showModal({
           title: '恭喜你',
           content: '图片保存成功',
           success: function (res) {
            if (res.confirm) {
             console.log('用户点击确定');
            } else if (res.cancel) {
             console.log('用户点击取消');
            }
           }
          });
         }
        }
       })
      },
      fail: function(res) {
       console.log(res)
       
      },
      complete: function() {
       that.canvasType = true
      }
     }, that)
    }, 500)
   }
  },
  onLoad() {
   let that = this;

   this.stickers = stickers
   this.chooseImages = chooseImages
   this.dom = document.getElementById('canvas')
  },
 }
</script>

